// src/components/Favorites/index.js
import React from 'react';

const Favorites = ({ section }) => {
  return (
    <div>
      {section === 'personal' && <div>我的收藏</div>}
      {section === 'recommend' && <div>推荐收藏</div>}
      {!section && <div>请选择收藏选项</div>}
    </div>
  );
};

export default Favorites;
