// src/components/Settings/index.js
import React from 'react';

const Settings = ({ section }) => {
  return (
    <div>
      {section === 'personal' && <div>个人设置内容</div>}
      {section === 'system' && <div>系统设置内容</div>}
      {!section && <div>默认设置内容</div>}
    </div>
  );
};

export default Settings;

